export async function getPostTags(postId: number): Promise<string[]> {
  const url = `${BLOGSERVICE_API}/tags?postId=${postId}`;

  const response = await fetch(url, {
    method: "GET",
  });

  if (response.ok) {
    return await response.json();
  } else {
    const errorText = await response.text().catch(() => " ")
    const errorMessage = errorText || `ÌÉúÍ∑∏ Ï°∞Ìöå Ïã§Ìå®: HTTP ${response.status} ÏùëÎãµ`;
    throw new Error(errorMessage);
  }
}

export const searchPosts = async (
    keyword: string,
    page: number = 0,
    size: number = 10
): Promise<PaginatedResponse<PostEntity>> => {
  try {
    const response = await fetch(
        `${BLOGSERVICE_API}/search?keyword=${encodeURIComponent(keyword)}&page=${page}&size=${size}`,
        {
          method: 'GET',
          headers: {
            'Content-Type': 'application/json',
          },
        }
    );

    if (!response.ok) {
      throw new Error('Failed to search posts');
    }

    return await response.json();
  } catch (error) {
    console.error('Error searching posts:', error);
    throw error;
  }
};

export async function uploadImage(file: File): Promise<{ url: string }> {
  const url = `${BLOGSERVICE_API}/upload`;

  const formData = new FormData();
  formData.append("file", file);

  const response = await fetch(url, {
    method: "POST",
    body: formData,
  });

  if (response.ok) {
    // Î∞±ÏóîÎìúÏóêÏÑú { "url": "..." } ÌòïÌÉúÏùò JSONÏùÑ Î∞òÌôò
    return await response.json();
  } else {
    const errorText = await response.text();
    throw new Error(errorText || `Ïù¥ÎØ∏ÏßÄ ÏóÖÎ°úÎìú Ïã§Ìå®: HTTP ${response.status}`);
  }
}

