export async function getFriendsPosts(userSignId: string, page: number = 0, size: number = 10): Promise<PaginatedResponse<PostEntity>> {
  const url = `${BLOGSERVICE_API}/feed?page=${page}&size=${size}`;
  const token = localStorage.getItem('accessToken');

  console.log("--- Friends Posts API Call Debug ---");
  console.log(`User Sign ID: ${userSignId}`);
  console.log(`Token Key Check: 'accessToken'`);
  console.log("------------------------------------");

  try {
    const response = await fetch(url, {
      method: "POST",
      credentials: "include",
      headers: {
        "userSignId": userSignId,
        "Content-Type": "application/json",
        "Authorization": `Bearer ${token}`,
      }
    });

    if (response.ok) {
      return await response.json();
    } else if (response.status === 403 || response.status === 404) {
      // ÏπúÍµ¨Í∞Ä ÏóÜÍ±∞ÎÇò Í∂åÌïúÏù¥ ÏóÜÎäî Í≤ΩÏö∞ Îπà Í≤∞Í≥º Î∞òÌôò
      console.log("No friends or no permission - returning empty result");
      return {
        content: [],
        pageable: {
          pageNumber: page,
          pageSize: size,
          sort: { empty: true, sorted: false, unsorted: true },
          offset: 0,
          paged: true,
          unpaged: false
        },
        last: true,
        totalPages: 0,
        totalElements: 0,
        size: size,
        number: page,
        sort: { empty: true, sorted: false, unsorted: true },
        first: true,
        numberOfElements: 0,
        empty: true
      };
    } else {
      // Îã§Î•∏ ÏóêÎü¨Ïùò Í≤ΩÏö∞ÏóêÎèÑ Îπà Í≤∞Í≥º Î∞òÌôò (ÏóêÎü¨Î•º throwÌïòÏßÄ ÏïäÏùå)
      const errorText = await response.text().catch(() => '');
      console.error(`Error ${response.status}:`, errorText);
      return {
        content: [],
        pageable: {
          pageNumber: page,
          pageSize: size,
          sort: { empty: true, sorted: false, unsorted: true },
          offset: 0,
          paged: true,
          unpaged: false
        },
        last: true,
        totalPages: 0,
        totalElements: 0,
        size: size,
        number: page,
        sort: { empty: true, sorted: false, unsorted: true },
        first: true,
        numberOfElements: 0,
        empty: true
      };
    }
  } catch (error) {
    console.error("Error fetching friends posts:", error);
    // ÎÑ§Ìä∏ÏõåÌÅ¨ ÏóêÎü¨ Îì±Ïùò Í≤ΩÏö∞ÏóêÎèÑ Îπà Í≤∞Í≥º Î∞òÌôò
    return {
      content: [],
      pageable: {
        pageNumber: page,
        pageSize: size,
        sort: { empty: true, sorted: false, unsorted: true },
        offset: 0,
        paged: true,
        unpaged: false
      },
      last: true,
      totalPages: 0,
      totalElements: 0,
      size: size,
      number: page,
      sort: { empty: true, sorted: false, unsorted: true },
      first: true,
      numberOfElements: 0,
      empty: true
    };
  }
}